import React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"

class BlogIndex extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title

    const talks = 
    [
      {
        title: `Solving Tricky Coordination Problems In Stateless .NET Services`,
        url: `https://www.youtube.com/watch?v=BO-SKMS-D_g`,
        date: "June 22, 2019",
        conf: "NDC Oslo"
      },
      {
        title: `The Right Tool for the Right Job: Redis Modules & Zig`,
        url: `https://www.youtube.com/watch?v=eCHM8-_poZY`,
        date: "June 22, 2019",
        conf: "RedisDay Tel Aviv 2019"
      },
      {
        title: `Implementing a New Data Structure for Redis as an Inexperienced C Programmer`,
        url: `https://www.youtube.com/watch?v=pmxaztRsoF4`,
        date: "June 22, 2019",
        conf: "RedisConf 2018"
      },
    ]

    const writings = 
    [
      {
        title: `Caches, Promises and Locks`,
        url: `https://redislabs.com/blog/caches-promises-locks/`,
        date: "May 29, 2019",
        committent: "Redis Labs - Tech Blog"
      },
      {
        title: `What to choose for your synchronous and asynchronous communication needs – Redis Streams, Redis Pub/Sub, Kafka etc.`,
        url: `https://redislabs.com/blog/what-to-choose-for-your-synchronous-and-asynchronous-communication-needs-redis-streams-redis-pub-sub-kafka-etc-best-approaches-synchronous-asynchronous-communication/`,
        date: "May 3, 2019",
        committent: "Redis Labs - Tech Blog"
      },
      {
        title: `Getting Started with Redis, Apache Spark and Python`,
        url: `https://redislabs.com/blog/getting-started-redis-apache-spark-python/`,
        date: "February 12, 2019",
        committent: "Redis Labs - Tech Blog"
      },
      {
        title: `How to Write a Redis Module in Zig`,
        url: `https://redislabs.com/blog/write-redis-module-zig/`,
        date: "January 23, 2019",
        committent: "Redis Labs - Tech Blog"
      },
      {
        title: `Redis: How Probabilistic Data Structures Support State-of-the-Art Apps`,
        url: `https://thenewstack.io/redis-how-probabilistic-data-structures-support-state-of-the-art-apps/`,
        date: "August 27, 2018",
        committent: "The New Stack"
      },
    ]

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO title="More content by me" />
        <h1>Talks</h1>
        {talks.map(({ title, url, date, conf }) => {
          return (
            <div>
              <h3
                style={{
                  marginBottom: rhythm(1 / 4),
                }}
              >
                <a style={{ boxShadow: `none` }} target="_blank" href={url}>
                  {title}
                </a>
              </h3>
              <small>{date} • {conf}</small>
            </div>
          )
        })}

        <h1>Articles</h1>
        {writings.map(({ title, url, date, committent }) => {
          return (
            <div>
              <h3
                style={{
                  marginBottom: rhythm(1 / 4),
                }}
              >
                <a style={{ boxShadow: `none` }} target="_blank" href={url}>
                  {title}
                </a>
              </h3>
              <small>{date} • {committent}</small>
            </div>
          )
        })}
      </Layout>
    )
  }
}

export default BlogIndex



export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`
